.input:focus {}

.input {
    height: 36px;
    width: 372px;
    border: 1px solid #D1D1D1;
    border-radius: 5px;
}

.inputWrapper {
    display: flex;
    flex-direction: column-reverse;

}

.superInput {
    border: 1px solid #D1D1D1;
    border-radius: 3px;

}

.errorInput {
    border: 1px solid #CC1439;
    border-radius: 3px;
}

.errorInput:focus {}

.error {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: #CC1439;
    margin-bottom: 5px;
}